.menu__list {
  list-style: none;
  display: flex;
  align-items: center;
  text-transform: uppercase;
  margin: 0;
  padding: 0;
}

.menu__item {
  position: relative;

  &:not(:first-child) {
    margin-left: 20px;
  }
}

.menu__link {
  font-size: 12px;
  font-weight: 500;
  text-align: left;
  display: flex;
  height: 60px;
  color: $main-font-color;
  align-items: center;
  text-decoration: none;

  &:hover {
    color: $text-color;
  }
}

.is-active {
  color: $text-color;

  &::after {
    display: block;
    content: "";
    height: 4px;
    width: 100%;
    background: #00acdc;
    position: absolute;
    border-radius: 8px;
    bottom: 0;
  }
}
